body{
    background-color: #000000d9 !important;
    color: white !important;
}

img{
    width: 100%;
}
#main{
    background-color: #0a3872;
}

.navbar-nav{
    width: 100%;
    justify-content: center;
}
#navigation{
    background-color: black !important;
}
.flexLine{
    display: flex;
    padding-top: 5vw;
    padding-bottom: 5vw;
    align-items: center;
}

#picture{
    width: 30%;
    padding-left: 5vw;
    padding-right: 5vw;
    align-items: center;
    justify-content: center;
    display: flex;
}

#me{
    width: 70%;
    padding-right: 5vw;
    padding-left: 5vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
}

#desc{
    font-size: clamp(15px, 2vw, 100px);
}

span{
    padding-left: 10px;
    padding-right: 10px;
}

#separation{
    height: 2px;
    background-color: white;
    margin-top: 5vh;
    margin-bottom: 5vh;
}

#icons{
    position: relative;
    display: flex;
    justify-content: center;
    overflow: hidden;
}

#icons svg{
    font-size: clamp(15px, 2vw, 120px);
    padding-right: 25px;
    padding-left: 25px;
    color: transparent;
    -webkit-animation: txt-appearance 0s 1.25s forwards;
            animation: txt-appearance 0s 1.25s forwards;
}

#icons svg:hover{
    color: black !important;
    -webkit-filter: drop-shadow(0 0 1rem black);
            filter: drop-shadow(0 0 1rem black);
}

p {
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 900;
    font-size: clamp(20px, 8vw, 120px);
    margin-bottom: 0 !important;
    line-height: 1;
}
p span {
    display: inline-block;
}

#first {
    font-weight: 300;
}

#second {
    color: black;
}

#icons::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    background: white;
    -webkit-animation: slide-in 0.75s 0.3s ease-out forwards,
    slide-out 0.75s 1.3s ease-out forwards;
            animation: slide-in 0.75s 0.3s ease-out forwards,
    slide-out 0.75s 1.3s ease-out forwards;
}

@-webkit-keyframes slide-in {
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}

@keyframes slide-in {
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}
@-webkit-keyframes slide-out {
    100% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%)
    }
}
@keyframes slide-out {
    100% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%)
    }
}

@-webkit-keyframes txt-appearance {
    100% {
        color: #ffffff;
    }
}

@keyframes txt-appearance {
    100% {
        color: #ffffff;
    }
}

@media (max-width: 950px) {
    .flexLine{
        flex-direction: column;
        justify-content: flex-start;
    }

    #me{
        flex-grow: 1;
    }

    #picture{
        width: 100%;
    }

    #icons svg {
        font-size: clamp(30px, 8vw, 100px)
    }

    #picture img{
        width: auto;
        height: 80%;
    }
}
#about{
    background-image: url(/cv-leo/static/media/fondAbout.a3ce3be0.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 5vw;
}

#cont{
    padding: 2vw;
    background-color: #0a3872d9 !important;
    border-radius: 30px;
}
